}

#header{
  margin-top: 4%;

  color: black !important;
  
}

.page-container{
  margin: 4% 8% 4% 8%;
}

.title-container{
  /* width: 50%; */
}

#about-container{
  width: 50%;
}

#about-container p{
  font-weight: 500;
  font-style: italic;
  font-size: 1rem;
  color: #eaeade;
}

.title{
  color: #eaeade;
  /* color: black !important; */
