  font-weight: 900;
  margin: 0 auto;
  /* -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #eaeade; */
}

#nav-container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top:15%;
  margin-bottom:15%;
  flex-wrap: wrap;
}

.row{
  display: flex;
  flex-direction: row;
  justify-content: flex-start; 
  align-items: center;
  width: 100%;
  font-size: 5rem;
  margin-top: 10%;
}

.row p{
  color: #eaeade;
  margin: 0;
}



.field-container{
  margin-top: 3%;
}

.field-container p{
  font-size: 3rem;
  color: #eaeade;
  margin: 0;
}

#submit-button{
  margin-top: 3%;
  width:25%;
  height: 50px;
  border: 1px solid black;
  outline: none;
  color: black;
  background-color: #eaeade;
  cursor: pointer;
  transition: 0.2s;
  font-family: 'Roboto', sans-serif;
}

#submit-button:hover{
  background-color: black;
  border: 1px solid #eaeade;
  color: #eaeade;
}

.form-container{
  margin: 0%;
  /* margin-top: 0;
  margin-bottom: 0;
  margin-left: 0; */
  /* text-align: left; */
}

.field-container input{
  background-color: #151515;
  outline: none;
  border: 1px solid #eaeade;
  width: 100%;
  height: 50px;
  color: #eaeade;
  /* padding: 5%; */
  border-right: 0;
  border-top: 0;
  border-left: 0;
  font-size: 2rem;
}

.field-container textarea{
  background-color: #151515;
  outline: none;
  border: 1px solid #eaeade;
  width: 100%;
  min-height: 150px;
  max-height: 250px;
  resize: vertical;
  color: #eaeade;
  padding: 1%;
  font-size: 2rem;
  font-family: 'Roboto', sans-serif;
}

::placeholder{
  color: #eaeade4c;
  font-size: 2rem;
  font-family: 'Roboto', sans-serif;
}

.link{
  cursor: pointer;
  transition: 0.2s;
}

