}
#page-break-text{
  color: #eaeade;
  font-size: 10rem;
  margin: 0 auto;
  margin-top: 5%;
  
  margin-bottom: 5%;
  /* line-break: anywhere; */

}

#page-break-text{
  line-height: 80%;
}

#info{
  width: 100%;
  margin-right: 5%;
}
.nav-child{
  width: 100%;
}

#about{
  margin-right: 5%;
}

.sub-categories{
  width: 80%;
}
.sub-categories p{
  font-size: 1rem;
  color: rgb(99, 99, 99);
  margin: 0;
}


#hamburger-container{
  position: absolute;
  top: 5%;
  right: 5%;
  cursor: pointer;
  z-index: 1000;
  position: fixed;
  /* background-color: red; */
  /* padding: 2%; */
  border: 0px solid #eaeade;
}

#hamburger-menu-container{

  /* background-color: red; */
  color: white;
  position: absolute;
  top: 0;
  right: 0;
  
  z-index: 100;
  height: 100%;
}
.ham-nav-items{
  position: fixed;
  top: 0;
  z-index: 100;
  margin: 0 auto;
  padding: 0;
  height: 100%;
  background-color: rgb(14, 14, 14);
  background-color: rgba(0, 0, 0, 0.95);
  background-color: #151515;
  /* border: 1px solid white; */
  color: white;
  font-size: 3rem;
  width: 100%;
  z-index: 100;
  
}



.slidebar-list-container{
  display: flex;
  justify-content: center;
  height: 100%;
  flex-direction: column;
  /* background: #99faf9; */
}
.slide-in{
  width: 100%;
  right: 0;
  display: block;
  animation: slide-in ease-in-out;
  animation-duration: 0.2s;
}

.slide-none{
  display: none;
}

.slide-out{
  width: 100%;
  display: block;
  animation: slide-out ease-in-out;
  animation-duration: 0.2s;
}

#slidebar-list{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  height: 100%;
  text-align: center;
  
}

.slidebar-menu-items{
  height: 100%;
  width: 100%;
  /* background: #99faf9; */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #eaeade;
  /* transition: 0.3s ease-in-out; */
}

.slidebar-menu-items p{
  font-size: 3rem;
}

.slidebar-menu-items:hover{
  
}


@keyframes slide-in{
  0%{
    right: -100%;
    
  }
  100%{
    right:0%;
    
  }
}

@keyframes slide-out{
  0%{
    right: 0;
  }
  100%{
    right:-100%
  }
}

.dot-animation{
  animation-name: dot;
  animation-duration: 30s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

@keyframes dot{
  0%{
    /* color: #ff0044; */
    color: #fc1b62
  }
  25%{
    color: #1bfce5
  }
  50%{
    color: #fff23d
  }
  75%{
    color: #1bfc53;
  }
  100%{
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;900&display=swap');
    color: #eaeade;
  }

}



#social-container{
  /* background-color: orange; */
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.fa-icon{
  color: white;
  opacity: 0.2;
  transition: 0.2s;
  cursor: pointer;
}
.fa-icon:hover{
  opacity: 1;
}


/* youtube */
#youtube-container{
  display: flex;
  justify-content: flex-start;
  width: 100%;
  margin-bottom: 5%;
}

#youtube-container iframe{
  width: 100%;
}

.content-child p{
  color: #eaeade;
  font-size: 5rem;
  margin-bottom: 0;
}

.sc-player{
  font-size: 10px; 
  color: #cccccc;
  line-break: anywhere;
  word-break: normal;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
   font-family: Interstate,Lucida Grande,Lucida Sans Unicode,Lucida Sans,Garuda,Verdana,Tahoma,sans-serif;
  font-weight: 100;
  
}
.sc-player a{
  color: #cccccc;
  text-decoration: none;
}

#master-music-container{
  display: flex;
  justify-content: space-between;
  align-self: flex-start;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
}

.child-music-container{
  margin-bottom: 5%;
}

.carousel{
  background-color: #cccccc;
}

.carousel-root{
  border: 5px solid #FF5836;
  border: 5px solid red;
}

.carousel-slide iframe{
  width: 100% !important;
  margin: 0 auto !important;
}

.cookiePolicy{
  display: none !important;
}

/* fade in sections */
.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

/* coding page */
.main-card-container{
  margin-top: 2%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

.card-button-container{
  width: 100%;
}

.card-button{
  background-color: transparent;
  border: 1px solid #eaeade;
  color: #eaeade;
  outline: none;
  padding: 5%;
  transition: 0.2s;
  cursor: pointer;
  /* width: 50%; */
}

.card-button:hover{
  background-color: #eaeade;
  color: #2b2b2b;
}

.card-child{
  /* max-width: 350px; */
  height: 70vh;
  padding: 2%;
  text-align: center;
  margin: 3%;
  margin-left: 0;
  background-color: #1c1c1c;
  /* background-color: #0c0c0c; */
  display: flex;
  /* flex-wrap: wrap; */
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  /* border: 1px solid #eaeade; */
  border-radius: 0px;
  color: #eaeade;
  box-shadow: 0px 1px 0px 0px #040404;
  transition: 0.2s;

  width: 350px;
  /* height: 500px; */
}



.card-child:hover{
  transform: translateY(-5px);
  box-shadow: 2px 10px 10px 0px #040404;
}

.card-image-container > img{
  opacity: 0.7;
  /* width: 40%; */
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s;
}



.card-child > img:hover{
  opacity: 1;
}

.language-container{
  /* display: flex;
  justify-content: center;
  flex-wrap: wrap; */

}

.card-language-image{
  width: 10%;
  height: auto;
  margin: 2%;
  border-radius: 5px;
}

footer{
  text-align: center;
  color: white;
  padding: 5%;
  background-color: #000000;
}



/* media queries */
@media only screen and (max-width:700px){

  body,html{
    font-size: 6px;
  }
  .row{
    flex-wrap:wrap
  }
  .page-container{
    margin: 15%;
  }
  .sub-categories p{
    font-size: 2rem;
  }
  .slidebar-menu-items p{
    font-size: 5rem;
  }
  .card-child-description{
    font-size: 2rem;
  }
  .card-child{
    /* height: 50vh; */
    padding: 5%;
    /* margin: 0 auto; */
  }
  footer{
    font-size: 2rem !important;
  }
}